.home{
  width: 100%;
  margin: auto;

  .worth{
    width: 90%;
    max-width: 1400px;
    margin: auto;
    margin: auto;
    display: flex;
    justify-content: space-between;

    @media (max-width: 900px) {
      display: block;
      text-align: center;
      width: 100%;
    }

    .left{
      display: inline-block;
      width: 50%;
      vertical-align: middle;
      @media (max-width: 600px) {
        width: 85%;
      }

      h1{
        color: var(--primary-highlight);
        font-family: 'Space Grotesk';
        font-weight: 500;
        font-size: 3.84rem;
        letter-spacing: -0.1rem;
        margin: 0px;
        margin-bottom: 30px;
      
        @media(max-width: 600px){
          font-size: 1.8rem;
          letter-spacing: -0.05rem;
          line-height: 2.5rem;
          margin-bottom: 2rem;
        }
      }
      p{
        color: var(--text-color);
        font-size: 1.9rem;
        line-height: 3.8rem;
        font-family: 'DM Sans';
        margin: 0px;
        max-width: 600px;
      
        @media(max-width: 600px){
          font-size: 1rem;
          line-height: 1.5rem;
        }
      }
    }
    
    .right{
      display: inline-block;
      width: 50%;
      max-width: 540px;
      vertical-align: middle;
      @media (max-width: 900px) {
        margin-top: 30px;
        width: 90%;
        text-align: center;
      }
    }
  }

  .banner-container{
    width: 100%;
    position: relative;

    .left-img{
      position: absolute;
      top: 20px;
      left: 0px;
      z-index: 1;
      max-width: 40vw;
    }

    .right-img{
      position: absolute;
      bottom: 200px;
      right: 0px;
      z-index: 1;
      max-width: 40vw;

      @media (max-width: 600px) {
        bottom: 100px;
      }
    }

    .banner{
      margin: auto;
      padding: 100px 0px 200px;
      width: 85%;
      max-width: 1400px;
      text-align: center;
      z-index: 2;
      position: relative;
  
      @media (max-width: 600px) {
        display: block;
        // text-align: left;
        padding: 100px 0px 100px;
      }
  
      .dynamic{

        @keyframes poof {
          0%{
            opacity: 1;
            transform: translate(-50%, 100%);
          }
          30%{
            opacity: 1;
            transform: translate(-50%, 100%);
          }
          35%{
            opacity: 1;
            transform: translate(-50%, 0%);
          }
          65%{
            opacity: 1;
            transform: translate(-50%, 0%);
          }
          70%{
            opacity: 0;
            transform: translate(-50%, -100%);
          }
          100%{
            opacity: 0;
            transform: translate(-50%, -100%);
          }
        }

        color: var(--primary-highlight);
        font-family: 'Space Grotesk';
        font-weight: 800;
        margin-bottom: 0.2rem;
        height: 4rem;
        position: relative;
        width: 100%;
        overflow: hidden;
        
        @media(max-width: 600px){
          height: 2rem;
        }

        h3{
          margin: 0rem;
          position: absolute;
          font-size: 3rem;
          line-height: 4rem;
          top: 0px;
          left: 50%;
          transform: translate(-50%, 0%);
          width: 100%;
          animation: poof 6s linear 0s infinite forwards;

          &:nth-child(1){
            animation-delay: 0s;
          }

          &:nth-child(2){
            animation-delay: -2s;
          }

          &:nth-child(3){
            animation-delay: -4s;
          }
        
          @media(max-width: 600px){
            font-size: 1.5rem;
            line-height: 2rem;
          }
        }
      }
  
      h1{
        color: var(--primary-highlight);
        font-family: 'Space Grotesk';
        font-weight: 800;
        font-size: 6rem;
        letter-spacing: -0.2rem;
        line-height: 7rem;
        margin: 0px;
        
        @media(max-width: 600px){
          font-size: 2.5rem;
          line-height: 3.2rem;
          letter-spacing: -0.1rem;
          margin-bottom: 2rem;
        }
      }
  
      h5{
        color: #9a9a9a;
        font-family: 'Space Grotesk';
        font-weight: 400;
        font-size: 1.68rem;
        width: 65%;
        margin: 0px auto;
        
        @media(max-width: 600px){
          font-size: 0.8rem;
          line-height: 2rem;
        }
      }
    
      p{
        color: var(--text-color);
        font-size: 2.16rem;
        line-height: 3.8rem;
        font-family: 'DM Sans';
        
        @media(max-width: 600px){
          font-size: 0.9rem;
          line-height: 2rem;
        }
      }
    }

  }

  .lessons{
    width: 90%;
    max-width: 1400px;
    margin: auto;
    margin-top: 100px;
    text-align: right;
    font-family: 'DM Sans';
    text-align: left;

    @media (max-width: 900px) {
      margin-top: 50px;
    }

    .next-lesson-container{
      text-align: center;
    }

    p{
      color: var(--primary-highlight);
      font-family: 'Space Grotesk';
      font-weight: 600;
      font-size: 2rem;

      @media (max-width: 600px){
        font-size: 1.5rem;
      }
    }

    h1{
      color: var(--primary-highlight);
      font-family: 'Space Grotesk';
      font-weight: 600;
      font-size: 60px;
      margin: 0px;
      margin-top: 4rem;

      @media (max-width: 1280px) {
        font-size: 60px;
      }

      @media (max-width: 480px) {
        font-size: 40px;
      }
    }
  }
}