.lesson{
    width: 33.33%;
    display: inline-block;
    padding: 10px 10px;
    box-sizing: border-box;
    margin-bottom: 40px;
    vertical-align: top;

    @media (max-width: 900px) {
      width: 50%;
    }

    @media (max-width: 480px) {
        width: 100%;
        padding: 0px;
    }

    .container{
        width: 100%;
        font-family: 'DM Sans';

        .header{
            color: var(--primary-highlight);
            font-family: 'Space Grotesk';
            font-weight: 600;
            text-align: center;
            overflow: hidden;
            font-size: 0rem;

            .index{
                text-align: left;
                font-size: 3.2rem;
                height: 3rem;
                opacity: 0.2;
                overflow: hidden;

                @media (max-width: 1280px) {
                    font-size: 3.2rem;
                    height: 3rem;
                }
            }

            img{
                width: 100%;
                margin: 0px;
                border-radius: 3px 3px 0px 0px;
                overflow: hidden;
            }

            .title{
                margin: 0px;
                background: #2D2D2D;
                border-radius: 0px 0px 3px 3px;
                padding: 10px 0px;
                display: inline-block;
                width: 100%;
                font-size: 2rem;

                @media (max-width: 1280px) {
                    font-size: 20px;
                }
            }
        }

        .description{
            margin: 15px 0px;
            padding: 0px 5px;
            font-size: 1.2rem;
            line-height: 2rem;
            text-align: left;

            @media (max-width: 1280px) {
                font-size: 1.2rem;
                line-height: 2rem;
            }
        }

        .list{
            margin: 0px 5px;
            width: calc(100% - 42px);
            border-style: solid;
            border-width: 0px 0px 0px 1px;
            color: var(--text-color);
            padding: 0px 0px 0px 16px;
            font-size: 16px;
            line-height: 28px;
            font-weight: bold;
            text-align: left;
            a{
                display: block;
                text-decoration: underline;
                font-size: 1.1rem;
                line-height: 2.8rem;
                color: var(--text-color);
            }
        }
    }
}