.calculator{
    border: 1px solid #2A2A2A;
    box-sizing: border-box;
    border-radius: 16px;
    width: 100%;
    margin: auto;
    padding: 32px;
    text-align: center;
    box-shadow: #00000040 0px 8px 16px 0px;
    font-size: 1rem;

    @media (max-width: 600px) {
        padding: 16px;
    }

    .result-container{
        display: flex;
        justify-content: space-between;

        .result{
            position: relative;
            display: inline-block;
            width: 48%;
            border: 1px solid #2A2A2A;
            box-sizing: border-box;
            border-radius: 16px;
            padding: 36px 0px 14px;
            font-family: 'DM Sans';
    
            .emoji{
                position: absolute;
                height: 48px;
                width: 48px;
                border-radius: 24px;
                background: #2A2A2A;
                font-size: 1.5rem;
                line-height: 48px;
                top: 0px;
                left: 50%;
                transform: translate(-50%, -50%);
            }
    
            .value{
                color: var(--primary-highlight);
                font-size: 1.8rem;
    
                @media (max-width: 600px) {
                    font-size: 1.2rem;
                }
            }
    
            p{
                font-size: 1rem;
            }
        }
    }

    p{
        font-size: 1rem;
        font-family: 'DM Sans';
    }

    h5{
        margin: 0px;
        font-family: 'DM Sans';
        font-weight: 400;
        margin-bottom: 2rem;
        font-size: 1.2rem;
    }

    .duration-container{
        position: relative;
        background: #3D3D3D;
        width: 100%;
        max-width: 644px;
        margin-bottom: 30px;
        border-radius: 0.2rem;

        .duration{
            font-size: 1rem;
            font-family: 'DM Sans';
            width: 20%;
            display: inline-block;
            text-align: center;
            padding: 8px 0px;
            z-index: 2;
            position: relative;
            cursor: pointer;

            @media (max-width: 600px) {
                font-size: 0.8rem;
            }
        
        }

        .highlight{
            width: 20%;
            position: absolute;
            height: 48px;
            background: #FF7A50;
            z-index: 1;
            top: calc(calc(100% - 48px) / 2);
            border-radius: 0.2rem;
            transition-duration: 0.5s;
            box-shadow: #ff7a5080 0px 4px 16px 0px;

            @media (max-width: 600px) {
                height: 40px;
                top: calc(calc(100% - 40px) / 2);
            }
        }
    }
    .variables{
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        margin-bottom: 30px;

        .variable{
            padding: 11px 16px;
            width: 48%;
            display: inline-block;
            border: 1px solid #2A2A2A;
            box-sizing: border-box;
            border-radius: 16px;
            text-align: left;
            font-family: 'DM Sans';
            
            label{
                font-size: 1rem;
            }
            .select{
                position: relative;
                width: 100%;

                .value{
                    font-size: 1.5rem;
                    padding: 2px;
                    color: var(--primary-highlight);
                    display: flex;
                    justify-content: space-between;
                    cursor: pointer;

                    @media (max-width: 600px){
                        font-size: 1.2rem;
                    }
                }

                .options{
                    position: absolute;
                    width: 100%;
                    border-radius: 0px 0px 4px 4px;
                    display: none;
                    overflow: hidden;
                    z-index: 3;
                    font-size: 1.5rem;

                    &.selected{
                        display: block;
                    }

                    .option{
                        padding: 5px 10px;
                        box-sizing: border-box;
                        background: black;
                        width: 100%;
                        background: var(--primary-highlight);
                        cursor: pointer;

                        @media (max-width: 600px){
                            font-size: 1.2rem;
                        }
                    }
                }
            }
        }
    }
    .toggle-container{
        width: 100%;
        border: 1px solid #2A2A2A;
        border-radius: 2px;
        padding: 10px 12px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;

        label{
            font-family: 'DM Sans';
            font-size: 14px;
        }

        .toggle{
            width: 40px;
            height: 24px;
            display: inline-block;
            border-radius: 15px;
            background: #3D3D3D;
            cursor: pointer;

            .icon{
                background: black;
                height: 20px;
                width: 20px;
                position: relative;
                top: 2px;
                left: 2px;
                border-radius: 12px;
                transition-duration: 0.5s;

                &.active{
                    background: var(--primary-highlight);
                    top: 2px;
                    left: 18px;
                }
            }
        }
    }
}